body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}


.reviewContainer{
  margin-top: 4%;
  margin-left: 40%;
  max-width: 20%;
  height: 700px;;
  background-color: #e6e6e6; 
  display: flex;
  flex-direction: column;
  border-radius: 10px;
  border: 1px solid;
  overflow-y: scroll;
}

.reviewBox{
  margin: 20px;
  border-bottom:  2px solid #8c8c8c;
}
.ratingBox{
  display: flex;
  flex-flow: row wrap;
 
}

.rating{
  margin-bottom: 10px;
  margin-left: 8px;
  flex: left 1;
}
.reviewDescription{
  margin: 10px;
  font-size: 10pt;
  font-weight: bold;
}

.author{
  font-style: italic;
  margin-bottom: 10px;
  margin-left: 70%;
}

.stars{
  color: #e600e6;
}

::-webkit-scrollbar {
  width: 0px;
  background-color: #8c8c8c;
  background: transparent; 
}
